
import XButton from 'vux/src/components/x-button/index.vue'

import { mapState, mapActions } from "vuex";
export default {
components: { XButton },
data() {
return {};
},
methods: {
loginOut() {
sessionStorage.setItem("userName", "");
sessionStorage.setItem("userToken", ""); 
this.$router.push({
name: "login_page"
});
}

},
mounted() {






},
beforeDestroy() {},
created() {},

computed: {
...mapState({
userName: state => state.login.userName
})
},
watch: {
$route(to, from) {}
}
};



